import React from 'react'

class App extends React.Component {
    constructor(){
        super()
        this.state = {
            isLoggedIn : false,
        }
    }

    handleClick() {
        console.log('Clicked!')
    }

    render() {
        return (
            <button onClick={this.handleClick}>LOG</button>
        )
    }
}

export default App